h1.heading {
  font-size: 60px;
  text-align: center;
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  margin-bottom: 8px;
}
p.heading.p__color {
  margin-bottom: 0;
  text-align: center;
  line-height: 1.2;
}
.skill__box {
  border-radius: 10px;
  width: 100%;
  padding: 55px;
  height:35vh ;
  background-color: #191919;
  transition: 0.4s ease-out;
}
p.skill__text.p__color {
  margin-bottom: 0;
  font-size: smaller;
}
.icon h4 {
  color: beige;
  margin-top: -10px;
}
.skill__box:hover {
  transform: translateY(5px);
}
.skill__box:hover .icon h4 {
  color: #f9004d;
}
.skill__box:hover p.skill__text.p__color {
  color: aliceblue !important;
}
/* responsive */
@media only screen and (max-width: 1024px) {
  .skill__box {
    padding: 20px;
    height: 30vh;
  }
  
}
@media only screen and (max-width: 900px){
  .container p {
    font-size: 12px !important;
    line-height: 1.2;
  }
  h1.heading {
    font-size: 2rem !important;
  }
  p.heading.p__color{
    line-height: 1.2;
    font-size: .9rem;
  }
}

@media only screen and (max-width: 280px) {
  .container p {
    font-size: 12px !important;
    line-height: 1.2;
  }
  .container h4 {
    font-size: 16px !important;
  }
  .skill__box:hover {
    transform: translateY(2px);
  }
  .skill__box:hover .icon h4 {
    color: #f9004d;
  }
  .skill__box:hover p.skill__text.p__color {
    color: aliceblue !important;
  }
} 
    
    
