/* @import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&family=Montserrat:wght@400;500;600&family=Poppins:wght@300;400&display=swap'); */
.home__bg{
     background: url("../images/31bg.jpg"); 
   /*  background: rgba(33, 33, 34, 0.942); */
    background-size: cover;
    height: 99vh;
    background-repeat: no-repeat;
    background-attachment: fixed;
   /*  background: rgb(22,21,23);
background: linear-gradient(120deg, rgba(22,21,23,1) 43%, rgba(121,9,32,1) 71%); */
}
li.nav__items{
    padding-bottom: 0px;
}
.home__content{
    padding: 50px 0px 150px 0px;
   
}
h1.home__text{
    font-size: 18px;
    font-weight: 400;
    letter-spacing: 3px;
    color: rgba(198, 201, 216, .75)!important;
}
h2.home__text{
    font-size: 54px;
    line-height: 54px;
    font-weight: 800;
    font-family: 'Montserrat', sans-serif;
}
a.nav__items{
    padding-bottom: 15px;
    transition: 0.4s ease-in-out;
}
.nav__items.active{
    font-size: 18px;
    border-bottom: 2px solid #f9004d;
    padding-bottom: 15px;
}
.nav__items:hover{
    color: #f9004d;
    font-size: 17px;
}
h3.home__text{
    font-size: 50px;
    font-weight: 700;
    font-family: 'Poppins', sans-serif;
    line-height: 40px;
}
.header{
    transition: .3s all ease-in-out;
}
.header.active{
    position: fixed;
    z-index: 1000;
    width: 100%;
    padding: 0px 30px;
   background: #111;
}
.toggle__menu{
    display: none;
}

.sidebar{
    transition: .4s all ease-in-out;
}
ul.sidebarc{
    display: flex;
    flex-wrap: wrap;
    position: fixed;
    height: 35%;
    right: 0%;
    width: 150px;  
    flex-direction: column;
    background: rgba(198, 201, 216, .4);
    justify-content: flex-start;
    
}
li.sidebar{
    padding: 10px 20px;
}
/*RESPONSIVE*/
@media only screen and (max-width:768px) {
    .header{
        justify-content:space-between;
    }
    h2.white.pz__10{
        font-size: 50px !important;
    }
    .navigation{
        display: none;
    }
    svg.bi.bi-justify{
        width: 30px;
        height: 30px;
    }
    .toggle__menu{
        display: flex;
        justify-content: flex-end;
        float: right;
        z-index: 1000;
        width:100%;
    }
    h2.home__text{
        font-size: 25px;
        font-weight: 600;
    }
    h3.home__text{
        font-size: 22px;
        font-weight: bold;

        font-family:sans-serif;
    }
}
 